import { useCart } from "./CartContext";

// import {CartContext} from "./CartContext"
export function ProductListing() {
  const { Addtocart } = useCart();

  return ["1", "2", "3", "4"].map((items) => (
    <div>
      <h2>Product {items}</h2>
      <button onClick={Addtocart}> Add to cart</button>
    </div>
  ));
}
